import React from 'react';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import DashboardIcon from '@mui/icons-material/Dashboard';
import TableChartIcon from '@mui/icons-material/TableChart';
import { Link } from 'react-router-dom';

export const Sidebar = (
    <React.Fragment>
        <ListSubheader component="div" inset>
            
        </ListSubheader>
        <ListItemButton component={Link} to="/">
            <ListItemIcon>
                <DashboardIcon />
            </ListItemIcon>
            <ListItemText primary="" />
        </ListItemButton>
        <ListItemButton component={Link} to="/about">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary=" " />
        </ListItemButton>
    </React.Fragment>
);

export const SecondSidebar = (
    <React.Fragment>
        <ListSubheader component="div" inset>
            
        </ListSubheader>
        <ListItemButton component={Link} to="/slideshow">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary="-" />
        </ListItemButton>
        <ListItemButton component={Link} to="/calculator">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary="" />
        </ListItemButton>
        <ListItemButton component={Link} to="/market">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary="" />
        </ListItemButton>
        <ListItemButton component={Link} to="/test">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary="" />
        </ListItemButton>
        <ListItemButton component={Link} to="/task5">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary=" 5" />
        </ListItemButton>
        <ListItemButton component={Link} to="/task6">
            <ListItemIcon>
                <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary=" 6" />
        </ListItemButton>
    </React.Fragment>
);

